import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import '@devsnc/sn-catalog-iframe';

createCustomElement('sn-component-catalog-item', {
	renderer: {
		type: snabbdom,
		view: (state) => {
			let {properties} = state;
			let {params={}} = properties;
			return (
					<sn-catalog-iframe sysId={properties.sysId} 
									table={properties.table} 
									tabId={properties.tabId} 
									parentSysId={params.sysparm_parent_sys_id || params.sysparmParentSysId}
									parentTable={params.sysparm_parent_table || params.sysparmParentTable}
									pageId={params.id || params.pageId}
									categoryId={params.sysId || params.sys_id || params.categoryId}/>
				)
		}
	},
	properties: {
		sysId: {
			reflect: true
		},
		table: {
			reflect: true
		},
		tabId: {
			reflect: true
		},
		params: {
			default : {
			}
		},
	}
});